import { useState } from "react";
import classes from "./SpecificationProduct.module.css";

const SpecificationProduct = (props) => {
  const [boxHeight, setBoxHeight] = useState(100);

  const boxHeightHandler = () => {
    setBoxHeight(390);
  };

  return (
    <div className="w-[90vw] m-auto">
      <p className="my-5 mr-4 font-bold text-gray-800">
         : <span>{props.name}</span>
      </p>
      <div className={boxHeight === 100 ? classes.before : "pb-5"}>
        <p
          className={`px-3 leading-7 text-justify text-gray-900 overflow-hidden `}
          style={{ height: `${boxHeight}px` }}
        >
                       
             .          
                      
                 .      
                        
                     
                  .      
                       
                    
                    . 
                       
            .          
                      
                 .      
                        
                     
                  .      
                       
                    
                    . 
                       
            .          
                      
                 .      
                        
                     
                  .      
                       
                    
                    . 
                       
            .          
                      
                 .
        </p>
      </div>
      {boxHeight !== 390 && (
        <button
          className="w-[90vw] m-auto py-2 font-bold"
          onClick={boxHeightHandler}
        >
           
        </button>
      )}
    </div>
  );
};

export default SpecificationProduct;
